.Estimate-view {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 16px;
    font-size: 40px;
    margin-top: 36px;
}

.Estimate-view span {
    width: 64px;
    height: 64px;
    display: grid;
    place-items: center;
    background-color: rgba(248, 27, 27, 0.329);
    border-radius: 8px;
}