.ItemItem{
    display: flex;
    position: relative;
    justify-content: center;
    list-style: none;
    font-size: 20px;
    background-color: rgba(255, 5, 5, 0.13);
    width: 80vw;
    padding: 8px;
    margin: 8px;
}

.ItemText {
    padding-left: 8px;
}

.ItemIconText{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 60vw;
}

.ItemIconText span {
    display: grid;
    place-items: center;
    font-size: 24px;
    width: 36px;
    height: 36px;
    background-color: rgba(248, 27, 27, 0.329);
    border-radius: 8px;
}

.IconDelete {
    position:absolute;
    top:-1vh;
    right: -12px;
    font-size: 28px;
}

.IconEdit {
    position: absolute;
    left: 12px;
    top: 33.3%;
    font-size: 24px;
}